// token 相關

export const setToken = (token) => {
  const expries = new Date(Date.now() + 1000 * 60 * 60 * 24 * 120).toGMTString();
  document.cookie = `admin=${token}; expires=${expries}; path=/`;
};

export const getToken = () => {
  const token = document.cookie.replace(/(?:(?:^|.*;\s*)admin\s*=\s*([^;]*).*$)|^.*$/, '$1');
  return token;
};

export const clearToken = () => {
  document.cookie = 'admin=;expires=;path=/';
};
