<template>
  <Navbar />
  <div class="container">
    <router-view />
  </div>
</template>

<script>
import Navbar from '@/components/layout/NavbarModel.vue';
import { getToken, clearToken } from '@/assets/javascript/methods';

export default {
  name: 'HomeView',
  data() {
    return {
    };
  },
  components: {
    Navbar,
  },
  methods: {
    // 確認登入狀態
    checkSignIn() {
      // 取得 cookie 中的 token
      const token = getToken();

      // 若有 token 則登入，反之轉為登入頁面
      if (token) {
        this.$http.defaults.headers.common.Authorization = `Bearer ${token}`;
      } else {
        this.$router.push('/login');
      }
    },
    // 登出
    signOut() {
      clearToken();
      this.$router.push('/signin');
    },
  },
  created() {
    this.checkSignIn();
  },
};
</script>
